.qima-modal-center {
  position: fixed !important;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.qima-modal-shadow {
  z-index: 0 !important;
  background: $qima-color-neutral-carbon;
  opacity: 0.8 !important;
}

.qima-modal-full {
  display: block !important;
  position: fixed !important;
  height: 100%;
  width: 100%;
}
