* ::-webkit-scrollbar-track {
  box-shadow: inset 0 0 2px $qima-winter-heaven;
  background-color: $qima-white-smoke;
  border-radius: $qima-spacing-m;
}

* ::-webkit-scrollbar {
  width: $qima-scrollbar-width;
  height: $qima-spacing-m;
}

* ::-webkit-scrollbar-thumb {
  @extend %qima-reduced-motion;
  @include qima-transition('background-color');

  border-radius: $qima-spacing-m;
  background-color: $qima-palladium;

  &:hover {
    cursor: pointer;
    background-color: $qima-basalt-grey;
  }

  &:focus {
    background-color: $qima-basalt-grey;
  }
}

* ::-webkit-scrollbar:horizontal {
  height: $qima-scrollbar-width;
}
