@mixin qima-font-faces($prefix: '../assets/') {
  @font-face {
    font-family: 'Inter';
    src:
      url($prefix + 'fonts/inter/regular/Inter-Regular.ttf') format('truetype'),
      url($prefix + 'fonts/inter/regular/Inter-Regular.eot') format('truetype'),
      url($prefix + 'fonts/inter/regular/Inter-Regular.woff') format('truetype'),
      url($prefix + 'fonts/inter/regular/Inter-Regular.woff2') format('truetype');
    font-weight: normal;
    font-display: swap;
  }
  @font-face {
    font-family: 'Inter';
    src:
      url($prefix + 'fonts/inter/semi-bold/Inter-SemiBold.ttf') format('truetype'),
      url($prefix + 'fonts/inter/semi-bold/Inter-SemiBold.eot') format('truetype'),
      url($prefix + 'fonts/inter/semi-bold/Inter-SemiBold.woff') format('truetype'),
      url($prefix + 'fonts/inter/semi-bold/Inter-SemiBold.woff2') format('truetype');
    font-weight: 600;
    font-display: swap;
  }
  @font-face {
    font-family: 'Inter';
    src:
      url($prefix + 'fonts/inter/bold/Inter-Bold.ttf') format('truetype'),
      url($prefix + 'fonts/inter/bold/Inter-Bold.eot') format('truetype'),
      url($prefix + 'fonts/inter/bold/Inter-Bold.woff') format('truetype'),
      url($prefix + 'fonts/inter/bold/Inter-Bold.woff2') format('truetype');
    font-weight: bold;
    font-display: swap;
  }
  @font-face {
    font-family: 'Noto Sans';
    src:
      local('Noto Sans SC Regular'), local('NotoSans-Regular'),
      url($prefix + 'fonts/noto/NotoSans/NotoSans-Regular.eot') format('truetype'),
      url($prefix + 'fonts/noto/NotoSans/NotoSans-Regular.woff2') format('truetype'),
      url($prefix + 'fonts/noto/NotoSans/NotoSans-Regular.woff') format('truetype'),
      url($prefix + 'fonts/noto/NotoSans/NotoSans-Regular.ttf') format('truetype');
    font-weight: normal;
    font-display: swap;
  }
  @font-face {
    font-family: 'Noto Sans';
    src:
      url($prefix + 'fonts/noto/NotoSans/NotoSans-Medium.eot') format('truetype'),
      url($prefix + 'fonts/noto/NotoSans/NotoSans-Medium.woff2') format('truetype'),
      url($prefix + 'fonts/noto/NotoSans/NotoSans-Medium.woff') format('truetype'),
      url($prefix + 'fonts/noto/NotoSans/NotoSans-Medium.ttf') format('truetype');
    font-weight: 600;
    font-display: swap;
  }
  @font-face {
    font-family: 'Noto Sans';
    src:
      url($prefix + 'fonts/noto/NotoSans/NotoSans-Bold.eot') format('truetype'),
      url($prefix + 'fonts/noto/NotoSans/NotoSans-Bold.woff2') format('truetype'),
      url($prefix + 'fonts/noto/NotoSans/NotoSans-Bold.woff') format('truetype'),
      url($prefix + 'fonts/noto/NotoSans/NotoSans-Bold.ttf') format('truetype');
    font-weight: bold;
    font-display: swap;
  }
}
