body {
  @extend %qima-scroll;

  color: $qima-midnight;
  // This is really important to define in pixel the font-size so that em units coming from the other component will work
  font-size: $qima-body-default-font-size;
  line-height: $qima-body-default-line-height;

  &.qima-font-family-noto {
    font-family: 'Noto Sans', sans-serif;

    input, button, textarea {
      font-family: 'Noto Sans', sans-serif;
    }
  }

  &.qima-font-family-inter {
    font-family: 'Inter', sans-serif;

    input, button, textarea {
      font-family: 'Inter', sans-serif;
    }
  }
}
