.qima-autolink {
  @extend %qima-reset-a;
  @extend %qima-reduced-motion;
  @include qima-transition('color');

  font-size: inherit;
  font-weight: inherit;
  font-style: inherit;
  font-family: inherit;
  line-height: inherit;
  text-decoration: underline;
  cursor: pointer;
  color: $qima-pool;

  &:focus,
  &:hover {
    color: $qima-pool-hover;
  }
}
